import ScenicListItem from '@/componets/ScenicListItem.vue'
export default {
  name: 'homeLike',
  props: {
    likeList: {
		type: Array,
		default: null
	}
  },
  components: {
    ScenicListItem
  },
  data () {
    return {
      tempLikeList: this.likeList
    }
  },
  methods: {
    seeMore () {
      this.tempLikeList = this.likeList
    },
    itemClick (item) {
      this.$emit('itemClick', item)
    }
  },
  computed: {
    showMore () {
      return this.likeList.length > 3 && this.tempLikeList.length !== this.likeList.length
    }
  },
  watch: {
    likeList (newVal, oldVal) {
      this.tempLikeList = newVal ? newVal.slice(0, 3) : []
	  this.tempLikeList.forEach((item, index) => {
		  item.isShowHot = index < 3
	  })
      return this.tempLikeList
    }
  }
}
